// __tests__/hooks/useActivityMetrics.test.js
import { renderHook, act } from '@testing-library/react-hooks';
import AsyncStorage from '@react-native-async-storage/async-storage';
import useActivityMetrics from '../../src/hooks/useActivityMetrics';

// Mock AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () => ({
  getItem: jest.fn(),
  setItem: jest.fn(),
}));

describe('useActivityMetrics', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    // Mock Date.now() pour des tests prÃ©visibles
    jest.spyOn(Date, 'now').mockReturnValue(1640995200000); // 2022-01-01 00:00:00
    jest.spyOn(Date.prototype, 'toDateString').mockReturnValue('Sat Jan 01 2022');
  });

  afterEach(() => {
    jest.restoreAllMocks();
  });

  describe('Chargement initial', () => {
    it('devrait charger les mÃ©triques depuis AsyncStorage', async () => {
      AsyncStorage.getItem
        .mockResolvedValueOnce('5') // current_streak
        .mockResolvedValueOnce('30') // today_minutes
        .mockResolvedValueOnce('Sat Jan 01 2022'); // last_time_date

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());

      await waitForNextUpdate();

      expect(result.current.currentStreak).toBe(5);
      expect(result.current.todayMinutes).toBe(30);
      expect(result.current.formattedTime).toBe('30min');
    });

    it('devrait rÃ©initialiser les minutes si c\'est un nouveau jour', async () => {
      AsyncStorage.getItem
        .mockResolvedValueOnce('3') // current_streak
        .mockResolvedValueOnce('45') // today_minutes
        .mockResolvedValueOnce('Fri Dec 31 2021'); // last_time_date (hier)

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());

      await waitForNextUpdate();

      expect(result.current.currentStreak).toBe(3);
      expect(result.current.todayMinutes).toBe(0);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '0');
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', 'Sat Jan 01 2022');
    });

    it('devrait utiliser des valeurs par dÃ©faut si aucune donnÃ©e n\'est stockÃ©e', async () => {
      AsyncStorage.getItem.mockResolvedValue(null);

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());

      await waitForNextUpdate();

      expect(result.current.currentStreak).toBe(0);
      expect(result.current.todayMinutes).toBe(0);
      expect(result.current.formattedTime).toBe('0min');
    });
  });

  describe('Gestion des sessions', () => {
    it('devrait dÃ©marrer une session', async () => {
      AsyncStorage.getItem.mockResolvedValue(null);
      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      act(() => {
        result.current.startSession('vocabulary');
      });

      // VÃ©rifier que la session a dÃ©marrÃ© (pas d'Ã©tat visible mais fonction appelÃ©e)
      expect(typeof result.current.startSession).toBe('function');
    });

    it('devrait terminer une session et mettre Ã  jour les minutes', async () => {
      AsyncStorage.getItem.mockResolvedValue('0');
      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      // DÃ©marrer une session
      act(() => {
        result.current.startSession('vocabulary');
      });

      // Simuler 2 minutes plus tard
      jest.spyOn(Date, 'now').mockReturnValue(1640995200000 + 120000); // +2 minutes

      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(2);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '2');
    });

    it('ne devrait pas mettre Ã  jour si aucune session n\'est active', async () => {
      AsyncStorage.getItem.mockResolvedValue('0');
      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(0);
      expect(AsyncStorage.setItem).not.toHaveBeenCalledWith('today_minutes', expect.any(String));
    });
  });

  describe('Gestion des streaks', () => {
    it('devrait incrÃ©menter le streak si l\'activitÃ© Ã©tait hier', async () => {
      AsyncStorage.getItem
        .mockResolvedValueOnce('3') // current_streak
        .mockResolvedValueOnce('0') // today_minutes
        .mockResolvedValueOnce('Sat Jan 01 2022') // last_time_date
        .mockResolvedValueOnce('Fri Dec 31 2021'); // last_activity_date (hier)

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(4);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '4');
    });

    it('devrait rÃ©initialiser le streak si l\'activitÃ© n\'Ã©tait pas hier', async () => {
      AsyncStorage.getItem
        .mockResolvedValueOnce('5') // current_streak
        .mockResolvedValueOnce('0') // today_minutes
        .mockResolvedValueOnce('Sat Jan 01 2022') // last_time_date
        .mockResolvedValueOnce('Wed Dec 29 2021'); // last_activity_date (il y a 3 jours)

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(1);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '1');
    });

    it('ne devrait pas mettre Ã  jour le streak si l\'activitÃ© est dÃ©jÃ  aujourd\'hui', async () => {
      AsyncStorage.getItem
        .mockResolvedValueOnce('3') // current_streak
        .mockResolvedValueOnce('0') // today_minutes
        .mockResolvedValueOnce('Sat Jan 01 2022') // last_time_date
        .mockResolvedValueOnce('Sat Jan 01 2022'); // last_activity_date (aujourd'hui)

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      const initialStreak = result.current.currentStreak;

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(initialStreak);
    });
  });

  describe('Formatage du temps', () => {
    it('devrait formater les minutes correctement', async () => {
      AsyncStorage.getItem.mockResolvedValue(null);
      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      // Test avec diffÃ©rentes valeurs
      const testCases = [
        { minutes: 0, expected: '0min' },
        { minutes: 30, expected: '30min' },
        { minutes: 60, expected: '1h' },
        { minutes: 90, expected: '1h30min' },
        { minutes: 120, expected: '2h' },
        { minutes: 150, expected: '2h30min' }
      ];

      for (const testCase of testCases) {
        // Simuler les minutes
        AsyncStorage.getItem
          .mockResolvedValueOnce('0') // current_streak
          .mockResolvedValueOnce(testCase.minutes.toString()) // today_minutes
          .mockResolvedValueOnce('Sat Jan 01 2022'); // last_time_date

        const { result: testResult, waitForNextUpdate: waitForUpdate } = renderHook(() => useActivityMetrics());
        await waitForUpdate();

        expect(testResult.current.formattedTime).toBe(testCase.expected);
      }
    });
  });

  describe('Tendances de streak', () => {
    it('devrait retourner les bonnes tendances selon le streak', async () => {
      const testCases = [
        { streak: 0, expected: null },
        { streak: 1, expected: 'ðŸ”¥ Continue!' },
        { streak: 3, expected: 'ðŸ’ª En forme!' },
        { streak: 7, expected: 'ðŸ† Incroyable!' },
        { streak: 10, expected: 'ðŸ† Incroyable!' }
      ];

      for (const testCase of testCases) {
        AsyncStorage.getItem
          .mockResolvedValueOnce(testCase.streak.toString()) // current_streak
          .mockResolvedValueOnce('0') // today_minutes
          .mockResolvedValueOnce('Sat Jan 01 2022'); // last_time_date

        const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
        await waitForNextUpdate();

        expect(result.current.streakTrend).toBe(testCase.expected);
      }
    });
  });

  describe('Gestion des erreurs', () => {
    it('devrait gÃ©rer les erreurs de AsyncStorage silencieusement', async () => {
      AsyncStorage.getItem.mockRejectedValue(new Error('Storage error'));

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      // Devrait utiliser les valeurs par dÃ©faut
      expect(result.current.currentStreak).toBe(0);
      expect(result.current.todayMinutes).toBe(0);
    });

    it('devrait gÃ©rer les erreurs lors de la sauvegarde', async () => {
      AsyncStorage.getItem.mockResolvedValue('0');
      AsyncStorage.setItem.mockRejectedValue(new Error('Save error'));

      const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
      await waitForNextUpdate();

      // Ne devrait pas planter
      await act(async () => {
        result.current.startSession('test');
        jest.spyOn(Date, 'now').mockReturnValue(1640995200000 + 60000);
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(1); // Ã‰tat local mis Ã  jour
    });
  });
});